.loginWrapper{
	width: 100%;
	height: 100%;
	background: #dfdfdf;
	display: flex;
	align-items: center;
	justify-content: center;
	.loginContent{
		width: 300px;
		height: 300px;
		background: #fff;
		border-radius: 10px;
		display: flex;
		flex-direction: column;
		padding: 25px;
		.logintitle{
			padding: 0 0 25px 0;
			font-weight: bold;
			font-size: 20px;
		}
	}
}


