html, body {}
img{
  border: 0;
  display: block;
}
textarea{
  resize: none !important;
}

#root{
  height: 100%;
  .ant-layout-content{ flex: 1; }
  .ant-tabs, .ant-tabs-content, .ant-tabs-tabpane{ height: 100%; }
  .ant-tabs-bar{ margin: 16px 0 }
  .ant-layout-sider-children{ 
    overflow-y: scroll; 
    height: 100%;
    background: #001529;
  }
  .wrapper{ height: 100%; }

  .ant-table-wrapper{ min-width: 1000px; }

  .goodTabs, .goodTabs .ant-tabs-content, .goodTabs .ant-tabs-tabpane{
      height: auto;
  }

  @import url('./components/loading');
  @import url('./components/sider');
  @import url('./components/header');
}

.trigger {
  font-size: 20px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color .3s;
}

.trHide{
  display: none;
}

.trigger:hover {
  color: #1890ff;
}

.cpwrapper{
  padding-top: 100px;
}

.avatar{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 15px;
}

.printWrap{
  .title{
    th{
      font-weight: bold;
      text-align: center;
    }
  }
  th{
    padding-right: 5px;
  }
}


.depothistorymodal{
  .ant-form-item{margin-bottom: 10px !important;}
}

.ant-advanced-search-form {
  padding: 24px;
  background: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
}

.ant-advanced-search-form .ant-form-item {
  display: flex;
}

.ant-advanced-search-form .ant-form-item-control-wrapper {
  flex: 1;
}

.ant-upload-select {
  float: left;
  color: red;
}

.flex-view {
  padding: 10px;
  background: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
}
.flex-view .ant-form-item {
  margin-bottom: 0;
}

.flex-form {
  padding: 24px;
  background: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
}

.flex-form .ant-form-item {
  display: flex;
}

.flex-form .ant-form-item-control-wrapper {
  flex: 1;
}

@import url('./pages/mgr');
@import url('./pages/role');
@import url('./pages/login');




